<template>
  <div>
    <van-nav-bar
      :title="title"
      :left-text="showBack"
      :left-arrow="showArrow"
      fixed
      right-text="添加"
      @click-right="onClickRight"
    />
    <van-cell-group>
      <van-cell
        v-for="(item,index) in datalist"
        :key="index"
        :title="item.instructions"
        :value="item.username"
        clickable
        is-link
        :to="'/passDetail?id='+item.id"
      />
    </van-cell-group>
    <van-loading type="spinner" class="loading-pos" color="#1989fa" v-if="loading"/>
  </div>
</template>

<script>
import { getAllData } from "@/api/api.js";
export default {
  name: "home",
  data() {
    return {
      datalist: [],
      loading:false
    };
  },
  computed: {
    title() {
      return this.$route.meta.title || "";
    },
    showBack() {
      return this.$route.meta.showBack || "";
    },
    showArrow() {
      return this.$route.meta.showArrow || false;
    }
  },
  created() {
    this.loading = true;
    getAllData()
      .then(res => {
          this.loading = false;
        if (res.code == "success") {
          this.datalist = res.message;
        }
      })
      .catch(err => {
          this.loading = false;
        this.$toast.fail(err);
      });
  },
  methods: {
      onClickRight(){
          this.$router.push({
              name:'addpass'
          })
      }
  },
};
</script>

